// app/home/page.tsx
"use client";

import { useState, useEffect } from "react";
import { createClient } from "@/lib/supabase/client";
import { Feed } from "@/components/feed/feed";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { AlertCircle, Users, Menu, Filter } from "lucide-react";
import { Skeleton } from "@/components/ui/skeleton";
import { Switch } from "@/components/ui/switch";
import { Label } from "@/components/ui/label";
import { Button } from "@/components/ui/button";
import {
  Sheet,
  SheetContent,
  SheetHeader,
  SheetTitle,
  SheetTrigger,
} from "@/components/ui/sheet";

export default function HomePage(): React.JSX.Element {
  const [user, setUser] = useState<any>(null);
  const [profile, setProfile] = useState<any>(null);
  const [followingUserIds, setFollowingUserIds] = useState<string[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [databaseError, setDatabaseError] = useState<string | null>(null);
  const [showFollowButtons, setShowFollowButtons] = useState(true);
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);

  const supabase = createClient();

  useEffect(() => {
    fetchUserData();
  }, []);

  async function fetchUserData() {
    try {
      setIsLoading(true);
      
      const { data: { user: currentUser } } = await supabase.auth.getUser();
      setUser(currentUser);

      if (currentUser) {
        const { data: userProfile } = await supabase
          .from("profiles")
          .select("*")
          .eq("id", currentUser.id)
          .maybeSingle();
        setProfile(userProfile);

        const { data: follows } = await supabase
          .from("follows")
          .select("following_id")
          .eq("follower_id", currentUser.id);
        
        const followingIds = follows?.map(follow => follow.following_id) || [];
        setFollowingUserIds([...followingIds, currentUser.id]);

        const { error: profileError } = await supabase
          .from("profiles")
          .select("id")
          .limit(1);
        
        if (profileError?.message?.includes("Could not find the table")) {
          setDatabaseError("database_not_setup");
        }
      }
    } catch (error) {
      console.error("Error fetching user data:", error);
    } finally {
      setIsLoading(false);
    }
  }

  const handleFollowChange = (userId: string, isFollowing: boolean) => {
    if (isFollowing) {
      setFollowingUserIds(prev => [...prev, userId]);
    } else {
      setFollowingUserIds(prev => prev.filter(id => id !== userId));
    }
  };

  if (isLoading) {
    return (
      <div className="p-4 max-w-2xl mx-auto">
        <div className="mb-6">
          <Skeleton className="h-8 w-48 mb-2" />
          <Skeleton className="h-4 w-64" />
        </div>
        <div className="space-y-4">
          {[...Array(3)].map((_, i) => (
            <div key={i} className="p-4 border rounded-lg">
              <div className="flex items-center gap-3 mb-4">
                <Skeleton className="h-10 w-10 rounded-full" />
                <div className="flex-1">
                  <Skeleton className="h-4 w-32 mb-1" />
                  <Skeleton className="h-3 w-24" />
                </div>
                <Skeleton className="h-8 w-20" />
              </div>
              <Skeleton className="h-16 w-full" />
            </div>
          ))}
        </div>
      </div>
    );
  }

  if (databaseError === "database_not_setup") {
    return (
      <div className="p-4 max-w-2xl mx-auto">
        <Alert>
          <AlertCircle className="h-4 w-4" />
          <AlertTitle>Database Setup Required</AlertTitle>
          <AlertDescription>
            The database tables haven't been created yet. Please run the SQL scripts in the{" "}
            <code className="rounded bg-muted px-1 py-0.5">scripts</code> folder to set up your database.
          </AlertDescription>
        </Alert>
      </div>
    );
  }

  const isAuthenticated = !!user;

  return (
    <div className="pb-20"> {/* Dodaj padding za bottom navigation */}
      {/* Mobile Header */}
      <div className="sticky top-14 z-30 bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60 border-b lg:hidden">
        <div className="p-4">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-xl font-bold">
                {isAuthenticated ? "Home" : "Explore Posts"}
              </h1>
              <p className="text-sm text-muted-foreground mt-1">
                {isAuthenticated 
                  ? "Your personalized feed" 
                  : "Sign in to see posts from people you follow"}
              </p>
            </div>
            
            {/* Mobile Filter Menu */}
            {isAuthenticated && (
              <Sheet>
                <SheetTrigger asChild>
                  <Button variant="outline" size="icon">
                    <Filter className="h-4 w-4" />
                  </Button>
                </SheetTrigger>
                <SheetContent side="right">
                  <SheetHeader>
                    <SheetTitle>Feed Settings</SheetTitle>
                  </SheetHeader>
                  <div className="mt-6 space-y-4">
                    <div className="flex items-center justify-between">
                      <div className="flex items-center gap-2">
                        <Users className="h-4 w-4" />
                        <span className="text-sm">Following: {followingUserIds.length - 1}</span>
                      </div>
                    </div>
                    <div className="flex items-center justify-between">
                      <Label htmlFor="mobile-follow-buttons" className="text-sm">
                        Show follow buttons
                      </Label>
                      <Switch
                        id="mobile-follow-buttons"
                        checked={showFollowButtons}
                        onCheckedChange={setShowFollowButtons}
                      />
                    </div>
                  </div>
                </SheetContent>
              </Sheet>
            )}
          </div>
        </div>
      </div>

      {/* Desktop Header */}
      <div className="hidden lg:block max-w-2xl mx-auto p-4">
        <div className="mb-8">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-3xl font-bold">
                {isAuthenticated ? "Home" : "Explore Posts"}
              </h1>
              <p className="text-muted-foreground mt-2">
                {isAuthenticated 
                  ? "Your personalized feed" 
                  : "Sign in to see posts from people you follow"}
              </p>
            </div>
            
            {isAuthenticated && (
              <div className="flex items-center gap-4">
                <div className="flex items-center gap-2 text-sm">
                  <Users className="h-4 w-4" />
                  <span>Following: {followingUserIds.length - 1}</span>
                </div>
                <div className="flex items-center gap-2">
                  <Switch
                    id="follow-buttons"
                    checked={showFollowButtons}
                    onCheckedChange={setShowFollowButtons}
                  />
                  <Label htmlFor="follow-buttons" className="text-sm">
                    Show follow buttons
                  </Label>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>

      {/* Feed */}
      <div className="max-w-2xl mx-auto lg:p-0 px-4">
        <Feed 
          userId={user?.id}
          followingUserIds={followingUserIds}
          isAuthenticated={isAuthenticated}
          showFollowButton={showFollowButtons}
          onFollowChange={handleFollowChange}
        />
      </div>
    </div>
  );
}